  it: {
    pageTitle: "Regole dell'Attivit",
    activityDescriptionTitle: "1. Descrizione dell'Attivit",
    activityDescriptionDesc1:
      'ReelShort lancer periodicamente varie attivit vantaggiose, in cui gli utenti possono partecipare e completare compiti per ottenere premi. Le monete premio ottenute completando i compiti possono essere utilizzate per sbloccare serie a pagamento.',
    activityDescriptionDesc2:
      'Le monete premio ottenute completando i compiti possono essere utilizzate per sbloccare serie a pagamento.',
    participationMethodTitle: '2. Metodo di Partecipazione',
    participationMethodDesc:
      'Le attivit sono aperte a tutti gli utenti registrati di ReelShort.',
    activityDetailsTitle: "3. Dettagli dell'Attivit",
    activityDetailsCheckIn: 'Check-in',
    activityDetailsCheckInDesc:
      "I premi di check-in possono essere riscattati a partire dalle 00:00 di ogni giorno.Pi giorni consecutivi di check-in si accumulano, maggiori saranno i premi.Se vi  un'interruzione nei check-in, il conteggio si resetter al giorno 1.",
    activityDetailsNewer: 'Vantaggi per i Nuovi Utenti',
    activityDetailsNewerDesc:
      'Gli utenti possono partecipare ai compiti di vantaggio per i nuovi arrivati entro 7 giorni dalla registrazione.I metodi di ricompensa includono, ma non si limitano a, compiti esclusivi per i nuovi arrivati (es. vantaggi per la raccolta di nuovi utenti, sconti di ricarica per i nuovi arrivati), con tipi e quantit specifici di premi come mostrato sulla pagina.',
    todayBenefits: `Vantaggi di Oggi`,
    todayBenefitsDesc:
      "L'elenco dei compiti giornalieri viene aggiornato alle 00:00 e include, ma non si limita a, compiti di visione giornalieri, navigazione di video/articoli e compiti speciali una tantum.Altri compiti vantaggiosi saranno introdotti periodicamente in futuro, quindi restate sintonizzati.",
    watchEewards: 'Premi per la visione',
    watchEewardsDesc1:
      'Gli utenti possono ricevere premi dopo aver guardato per una durata specifica contenuti video come drammi, film o documentari sulla piattaforma. (Le serie interattive non sono incluse.)',
    watchEewardsDesc2:
      'Comportamenti anomali come la navigazione rapida dei video, la visione ripetuta o la pausa durante la riproduzione saranno considerati non validi.La piattaforma si riserva il diritto di non conteggiare tali comportamenti per la durata valida del compito o di non distribuire i premi.',
    watchAdsRewards: 'Premi per la visione degli annunci',
    watchAdsRewardsDesc:
      'Gli utenti possono richiedere premi dopo aver navigato le pagine/video specificati sulla piattaforma e soddisfatto i requisiti del compito.',
    otherTask: 'Altri premi',
    otherTaskDesc:
      'Le informazioni dettagliate sugli altri compiti saranno soggette alle regole specifiche di ogni compito.Le questioni non specificate nelle regole specifiche dei compiti saranno disciplinate da queste Regole delle Attivit.',
    importantNotesTitle: '4. Note Importanti',
    importantNotesDesc1:
      'ReelShort si riserva il diritto di interpretazione finale di questo evento.',
    importantNotesDesc2:
      'A causa di limitazioni legate al dispositivo, alla posizione, alla versione del client e ad altri fattori, diversi utenti potrebbero vedere compiti differenti.Grazie per la vostra comprensione.',
    importantNotesDesc3:
      "La piattaforma determiner se gli utenti sono gli stessi basandosi su dispositivo, account e altre informazioni.Quando considerati come lo stesso utente, il criterio per l'emissione dei premi sar basato se i premi sono stati assegnati a quel dispositivo.",
    importantNotesDesc4:
      "La piattaforma dispone di due modi per emettere premi: distribuzione automatica del sistema e raccolta manuale dell'utente.Per i compiti che richiedono la raccolta manuale, gli utenti devono richiedere i loro premi entro un tempo specificato (vedi la pagina dell'attivit per i dettagli).Se non richiesti in tempo, sar considerata una rinuncia volontaria al premio, e i premi non richiesti saranno automaticamente cancellati.",
    contactInformationTitle: '5. Informazioni di contatto',
    contactInformationDesc:
      'Se hai domande, vai su [Profilo] > [Feedback] per fornire un feedback.'
  },
